.App {
  text-align: center;
}

div {
  box-sizing: border-box;
}

.black-nav {
  display: flex;
  background-color: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
}

button {
  margin: 10px;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background-color: #eee;
  text-align: left;
}

span:hover {
  cursor: pointer;
}

h4:hover {
  cursor: pointer;
}
